@import url(https://fonts.googleapis.com/css2?family=Nunito:ital,wght@0,300;0,400;0,600;0,700;0,800;1,700;1,800&display=swap);
body {
  margin: 0;
  font-family: 'Nunito', sans-serif;
}

.App{display:flex;flex-direction:column;justify-content:center;align-items:center}

.View{display:flex;width:100%;height:100vh}

.Decorate{background-color:#D7D7D7;width:50vw;display:flex;flex-direction:column;justify-content:center;align-items:center}.Decorate__container{display:block;flex-direction:column;justify-content:center;align-items:center}.Decorate__image{max-width:600px}

.Header{text-align:center;font-size:36px;font-weight:700;margin:48px 0 0 0}.Header__w{color:#F5C001}

.Content{display:flex;flex-direction:column;justify-content:center;align-items:center;background-color:#171820;color:white;width:50vw}.Content__header{margin:0 0 32px 0;font-size:48px;font-weight:700;text-align:center;max-width:500px}.Content__paragraph{text-align:center;margin:0 0 64px 0;font-size:24px;font-weight:500;line-height:36px;max-width:500px}.Content__span{color:#FFE321}

.Slider{font-weight:800;font-style:italic;color:white;text-align:center;font-size:72px}.Slider__yellow{color:#FFE321}

.Button{width:224px;height:56px;border-radius:30px;font-size:30px;font-weight:700;font-family:'Nunito', sans-serif;background:linear-gradient(180deg, #FFE321 0%, #F5C001 85.94%);box-shadow:0 0 10px rgba(0,0,0,0.25)}

